.header {
    height: 70px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
  
    .logo-container {
      height: 100%;
      width: 500px;
      padding: 25px;
      font-size: 30px;

        &:hover {
          text-decoration: none;
        }

    }


  
    .options {
      width: 50%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
  
      .option {
        padding: 10px 15px;
      }

      .option:hover {
        cursor: pointer;
        text-decoration: underline;
      }
    }
  }
  